.center {
  display: flex;
  justify-content: center;
  width: 100vw;
  position: absolute;
  top: 10%;
}

.container {
  padding: 0 2rem;
  width: 100%;
  color: var(--primary-black);
  font-family: var(--primary-font);
}

.container p {
  font-size: 4rem;
  font-family: var(--primary-font);
  font-weight: 700;
  padding: 2rem;
}

.Hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Hero p {
  font-size: 1.3rem;
  font-family: var(--primary-font);
  font-weight: 400;
}

.Img {
  width: 400px;
  height: 300px;
  object-fit: contain;
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
  .container h1 {
    font-size: 3rem;
  }
}

@media (min-width: 992px) {
  .container {
    width: 992px;
  }
  .container h1 {
    font-size: 3.5rem;
  }
  .Hero {
    flex-direction: row;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
  .container h1 {
    font-size: 4rem;
  }
}
