.center {
  display: flex;
  justify-content: center;
  width: 100vw;
  position: absolute;
  top: 10%;
}

.container {
  padding: 0 2rem;
  width: 100%;
  color: var(--primary-black);
  font-family: var(--primary-font);
}

.ImgContainer {
  width: 100%;
  position: relative;
}

.Img {
  width: 100%;
  height: 50%;
  object-fit: contain;
}

.Hero {
  position: absolute;
  font-weight: 700;
  z-index: 100;
  font-size: 2rem;
  font-family: var(--primary-font);
  color: var(--primary-orange);
  top: 45%;
  left: 0%;
}

.Text {
  text-align: center;
  margin-top: 2rem;
}

.Text p {
  font-size: 1.2rem;
  font-family: var(--primary-font);
  color: var(--primary-black);
}

.Provide {
  text-align: center;
  padding: 1rem;
}

.Provide li {
  font-size: 1.3rem;
  font-family: var(--secondary-font);
  padding: 0.1rem;
  color: var(--primary-orange);
}

.Yo {
  text-align: center;
}

.Yo p {
  font-size: 1.3rem;
  font-family: var(--primary-font);
  color: var(--primary-black);
  font-weight: 600;
  padding: 1rem;
}

.Yo button {
  padding: 1rem 2rem;
  background: #000000;
  color: var(--primary-orange);
  font-family: var(--primary-font);
  font-size: 1rem;
  border: 1px solid var(--primary-orange);
  transition: var(--transition);
  border-radius: 20px;
  cursor: pointer;
}

.Yo button:hover {
  background: var(--primary-white);
  color: #000000;
}

.Yo button:focus {
  outline: none;
}

.Footer {
  margin-top: 3rem;
  text-align: center;
  font-size: 1rem;
  font-family: var(--primary-font);
}

.Footer p {
  padding: 0.5rem 0;
}

.Footer a {
  color: var(--primary-orange);
}

@media (min-width: 768px) {
  .container {
    width: 768px;
  }
  .container h1 {
    font-size: 3rem;
  }
  .Grid {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 1rem;
    grid-template-areas:
      "dog bow"
      "meow cat";
  }
  .Hero {
    font-size: 2.5rem;
  }

  .Text p {
    font-size: 1.5rem;
  }
}

@media (min-width: 992px) {
  .container {
    width: 992px;
  }
  .container h1 {
    font-size: 3.5rem;
  }
  .Hero {
    font-size: 3.5rem;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
  .container h1 {
    font-size: 4rem;
  }
  .Hero {
    top: 40%;
    font-size: 5rem;
  }
}
